<template>
    <div class="better-button" @click="$emit('changeTurn')" :class="{ col: black, animationWhite: doWhiteAnimation, animationBlack: doBlackAnimation }">
        <slot></slot>
    </div>
</template>
<script>
export default {
    name: "Half",
    emits: ["changeTurn"],

    props: {
        black: Boolean,
        totalTime: Number,
        turn: String,
        paused: Boolean,
    },
    components: {},
    mounted() {
        console.log(this.black);
    },
    data() {},
    computed: {
        transitionBackground() {
            let slightChange = Math.sin(((this.totalTime * 3.14) / 180.0) * 15) * 20 + 20;
            let otherChange = 255 - slightChange;
            if (this.black) {
                if (this.turn == "black") {
                    return "rgb(" + slightChange + "," + slightChange + "," + slightChange + ")";
                }
                return "rgb(0,0,0)";
            } else {
                if (this.turn == "white") {
                    return "rgb(" + otherChange + "," + otherChange + "," + otherChange + ")";
                }
                return "rgb(255,255,255)";
            }
        },
        doWhiteAnimation() {
            if (!this.black && this.turn == "white" && !this.paused) {
                return true;
            } else {
                return false;
            }
        },
        doBlackAnimation() {
            if (this.black && this.turn == "black" && !this.paused) {
                return true;
            } else {
                return false;
            }
        },
    },
    methods: {},
};
</script>

<style scoped>
@media only screen and (max-width: 800px) {
    .better-button {
        width: 100% !important;
        height: 50% !important;
    }
}

