<template class="full-size">
    <div v-if="!started" class="flexy-card">
        <div class="frosted-card">
            <h1>Welcome</h1>
            <div class="not-centered">
                <span class="bold">How much time per side?</span>
                <br />
                <span>
                    Minutes:
                    <input placeholder="minutes" type="number" v-model="totalMinutes" />
                </span>
                <div class="slight-break" />
                <span>
                    Seconds:
                    <input placeholder="seconds" type="number" v-model="totalSeconds" />
                </span>

                <br />
                <br />
                <span class="bold">
                    How much increment?
                </span>
                <br />
                <span>Seconds: <input v-model="totalIncrement" type="number" placeholder="seconds"/></span>
                <br />
                <br />
                Right now, each side will have {{ totalMinutes }} minutes, {{ totalSeconds }} seconds, and {{ totalIncrement }} extra seconds per move.
                <br />
                To make your move, hit any key on your side of the keyboard (or click on your side).
                <br />
            </div>
            <br />
            <button @click="start" class="start-button">Start</button>
            <br /><br />
        </div>

        <!-- <canvas id="background-graphics" width="100vw" height="100vh"> </canvas> -->
        <img src="../public/pawn.svg" id="first" />
        <img src="../public/queen.svg" id="second" />
        <img src="../public/king.svg" id="third" />
    </div>
    <div v-else @keyup.space="swap" class="full-size">
        <div class="flexy">
            <Half :black="false" :totalTime="tick" :paused="paused" :turn="turn" @changeTurn="checkSwap('white')">
                <img src="../public/crown.png" class="crown" v-show="showWhiteCrown" />
                {{ getWhiteMinute }}:{{ getWhiteSeconds }}</Half
            >
            <Half :black="true" :totalTime="tick" :paused="paused" :turn="turn" @changeTurn="checkSwap('black')">
                <img src="../public/crown.png" class="crown" v-show="showBlackCrown" />
                {{ getBlackMinute }}:{{ getBlackSeconds }}</Half
            >
        </div>
        <transition name="fade">
            <div class="warning-message" v-show="showMessage">{{ message }}</div>
        </transition>
        <!-- <button @click="swap">Swap</button> -->
        <div class="funkyFlex">
            <div @click="pause" class="stop-button"><span v-if="paused">Start</span><span v-else>Pause</span></div>
            <br />
            <div @click="stop" class="pause-button">Stop</div>
        </div>
    </div>
</template>

<script>
import Half from "./components/Half.vue";

export default {
    name: "App",
    components: { Half },

    data() {
        return {
            test: "hello",
            totalMinutes: 10,
            totalSeconds: 0,
            totalIncrement: 10,
            whiteTime: 0,
            blackTime: 0,
            started: false,
            startedInterval: null,
            tick: 0,
            turn: "white",
            paused: true,
            message: "",
            whiteKeyNeeded: ["ControlLeft", "ShiftLeft", "CapsLock", "Tab", "Backquote", "Digit1", "KeyQ", "KeyA", "KeyZ", "AltLeft", "MetaLeft", "KeyX", "KeyS", "KeyW", "Digit2", "Digit3", "KeyE", "KeyD", "KeyC", "KeyV", "KeyF", "KeyR", "Digit4", "Digit5", "KeyT", "KeyG", "KeyB", "Space"],
            blackKeyNeeded: [
                "Digit6",
                "KeyY",
                "KeyH",
                "KeyN",
                "KeyM",
                "KeyJ",
                "KeyU",
                "Digit7",
                "Digit8",
                "KeyI",
                "KeyK",
                "Comma",
                "Period",
                "KeyL",
                "KeyO",
                "Digit9",
                "Digit0",
                "KeyP",
                "Semicolon",
                "Slash",
                "Quote",
                "BracketLeft",
                "BracketRight",
                "Minus",
                "Equal",
                "Backspace",
                "Backslash",
                "Enter",
                "ShiftRight",
                "ArrowLeft",
                "ArrowDown",
                "ArrowUp",
                "ArrowRight",
                "AltRight",
                "MetaRight",
                "Space",
            ],
            showMessage: false,
            showWhiteCrown: false,
            showBlackCrown: false,
        };
    },
    computed: {
        getBlackMinute() {
            return Math.floor(this.blackTime / 60);
        },
        getBlackSeconds() {
            let rounded = Math.round((this.blackTime % 60) * 10) / 10;
            if (Math.round(rounded) == rounded) {
                rounded = rounded + ".0";
            }
            if (rounded > 9.9) {
                return rounded;
            } else {
                return "0" + rounded;
            }
        },
        getWhiteMinute() {
            return Math.floor(this.whiteTime / 60);
        },
        getWhiteSeconds() {
            let rounded = Math.round((this.whiteTime % 60) * 10) / 10;
            if (Math.round(rounded) == rounded) {
                rounded = rounded + ".0";
            }
            if (rounded > 9.9) {
                return rounded;
            } else {
                return "0" + rounded;
            }
        },
    },
    methods: {
        pause() {
            if (this.paused) {
                this.paused = false;
            } else {
                this.paused = true;
            }
        },
        checkSwap(enteredFrom) {
            if (enteredFrom == this.turn && !this.paused) {
                this.swap();
            } else {
                this.message = "It is " + this.turn + "'s turn";
                this.showMessage = true;
                setTimeout(() => {
                    this.showMessage = false;
                }, 1000);
            }
        },
        start() {
            this.whiteTime = parseInt(this.totalMinutes) * 60 + parseInt(this.totalSeconds);
            this.blackTime = parseInt(this.totalMinutes) * 60 + parseInt(this.totalSeconds);
            this.paused = true;
            this.started = true;
            this.startedInterval = setInterval(() => {
                this.tick++;
                console.log(this.tick);
                if (!this.paused) {
                    if (this.whiteTime == 0) {
                        this.paused = true;
                        this.message = "Black has won!";
                        this.showBlackCrown = true;
                        this.showMessage = true;
                        setTimeout(() => {
                            this.showMessage = false;
                        }, 3000);
                    } else if (this.blackTime == 0) {
                        this.paused = true;
                        this.showWhiteCrown = true;

                        this.message = "White has won!";
                        this.showMessage = true;
                        setTimeout(() => {
                            this.showMessage = false;
                        }, 3000);
                    } else {
                        if (this.turn == "white") {
                            this.whiteTime = Math.round(this.whiteTime * 10 - 1) / 10;
                        } else {
                            this.blackTime = Math.round(this.blackTime * 10 - 1) / 10;
                        }
                    }
                }
            }, 100);
            window.addEventListener("keydown", this.keyBinds);
        },
        keyBinds(e) {
            this.message = "";
            if (this.paused) {
                this.message = "The game is paused";
                this.showMessage = true;
                setTimeout(() => {
                    this.message = "";
                    this.showMessage = false;
                }, 1000);
                return;
            }

            if (e.code == "Space") {
                this.swap();
                return;
            }
            if (this.whiteKeyNeeded.includes(e.code)) {
                if (this.turn == "white") {
                    this.swap();
                } else {
                    // SAY ITS NOT THEIR TURN YET
                    this.message = "It is " + this.turn + "'s turn";
                    this.showMessage = true;
                    setTimeout(() => {
                        this.showMessage = false;
                    }, 1000);
                }
            } else if (this.blackKeyNeeded.includes(e.code)) {
                if (this.turn == "black") {
                    this.swap();
                } else {
                    // Say ITS NOT THEIR TURN YET
                    this.message = "It is " + this.turn + "'s turn";
                    this.showMessage = true;

                    setTimeout(() => {
                        this.message = "";
                        this.showMessage = false;
                    }, 1000);
                }
            }
        },
        stop() {
            this.whiteTime = 0;
            this.blackTime = 0;
            this.started = false;
            this.turn = "white";
            this.showWhiteCrown = false;
            this.showBlackCrown = false;

            clearInterval(this.startedInterval);
            window.removeEventListener("keydown", this.keyBinds);
        },
        swap() {
            if (this.turn == "white") {
                this.turn = "black";
                this.whiteTime += parseInt(this.totalIncrement);
            } else {
                this.turn = "white";
                this.blackTime += parseInt(this.totalIncrement);
            }
        },
    },
};
</script>

